export const query = graphql`
    query { 
        allHnStory { 
            edges { 
              node { 
                id
                title
                score
                order
                domain
                url
              }
            }
          }
    }
`

export default ({data}) => (
    <div>
        {data.allHnStory.edges.map(edge => {
            const story = edge.node
            return (
                <a style={{ color: 'inherit' }} href={story.url}>
                    {story.title}
                    <small style={{ display: 'block' }}>
                        {story.score} Point {story.domain}
                    </small>
                </a>
            )
        })}
    </div>
)